/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: 'Google Sans';
  src: url("../fonts/GoogleSans-Regular.woff2") format("woff2"), url("../fonts/GoogleSans-Regular.woff") format("woff"), url("../fonts/GoogleSans-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Google Sans';
  src: url("../fonts/GoogleSans-Medium.woff2") format("woff2"), url("../fonts/GoogleSans-Medium.woff") format("woff"), url("../fonts/GoogleSans-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Google Sans';
  src: url("../fonts/GoogleSans-Bold.woff2") format("woff2"), url("../fonts/GoogleSans-Bold.woff") format("woff"), url("../fonts/GoogleSans-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

.block-title {
  font-weight: bold;
  font-size: 3rem;
  line-height: 1.3;
  color: #fff;
}

.block-title--company {
  position: relative;
}

.block-title--company:before {
  content: '';
  position: absolute;
  left: 260px;
  background: url("../images/logo-red.svg") no-repeat center;
  background-size: 100%;
  width: 230px;
  height: 53px;
}

@media screen and (max-width: 769px) {
  .block-title--company:before {
    width: 100px;
    left: 200px;
    height: 50px;
    background-size: 100%;
  }
}

.text {
  line-height: 1.6;
  font-size: 1.125rem;
  max-width: 485px;
  color: #fff;
  margin-top: 50px;
}

.text--extra-margin {
  font-size: 1rem;
  color: #f2f2f2;
  margin-left: 35px;
  max-width: 485px;
  line-height: 1.8;
  font-weight: 300;
  margin-top: 3.125rem;
}

.text--accent {
  font-weight: bold;
  color: #C2F9FF;
  font-size: 1.375rem;
  margin-top: 3.125rem;
  max-width: 485px;
  line-height: 1.4;
}

.flex-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  padding: 0 20px;
  flex-wrap: wrap;
  margin: 0 auto;
}

@media screen and (max-width: 991px) {
  .flex-container {
    justify-content: space-around;
  }
}

.about {
  position: relative;
  top: -80px;
  z-index: 2;
  padding: 100px 0 230px;
}

@media screen and (max-width: 769px) {
  .about {
    padding: 70px 0 100px;
  }
}

.about::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  background-color: #313E50;
  transform: skewY(-4deg);
  width: 120%;
  height: 100%;
}

.about::after {
  content: url("../images/bg-figure-2.svg");
  position: absolute;
  bottom: -50px;
  transform: skewY(2.5deg);
  right: 0;
  z-index: 0;
}

@media screen and (max-width: 769px) {
  .about::after {
    content: '';
  }
}

.button {
  background-color: #FB0201;
  color: #fff;
  font-weight: bold;
  font-size: 1.375rem;
  text-align: center;
  padding: 10px;
  width: 220px;
  margin-top: 40px;
  border: 0;
  cursor: pointer;
  transition: box-shadow 0.15s;
}

.button:hover {
  box-shadow: 5px 5px 30px rgba(251, 2, 1, 0.6);
}

.catalog {
  position: relative;
  padding: 140px 0 280px;
  top: -1px;
}

.catalog::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  background-color: #1F2733;
  transform: skewY(-4deg);
  width: 120%;
  height: 100%;
}

@media screen and (max-width: 769px) {
  .catalog .text--accent {
    margin-top: 80px;
  }
}

.catalog::after {
  content: '';
  position: absolute;
  bottom: 0px;
  background-color: #1F2733;
  width: 100%;
  height: 50px;
}

.catalog-product {
  position: relative;
}

.catalog-product__img {
  width: 270px;
  height: 200px;
  margin-top: 30px;
  object-fit: cover;
}

@media screen and (max-width: 769px) {
  .catalog-product__img {
    margin-top: 40px;
  }
}

.catalog-product__info {
  position: absolute;
  background: #fff;
  padding: 34px 10px 0 20px;
  top: 110%;
  left: 0;
  z-index: 1;
  transform: translate(-50%, -50%);
  height: 250px;
  width: 170px;
}

@media screen and (max-width: 769px) {
  .catalog-product__info {
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.catalog-product__info-heading {
  font-size: 14px;
  margin-top: 25px;
}

.catalog-product__info-heading:first-child {
  margin-top: 0;
}

.catalog-product__info-content {
  font-size: 1.375rem;
  font-weight: bold;
  line-height: 1.5;
  margin-top: 10px;
}

.catalog-product__info-link {
  background-color: #FB0201;
  color: #fff;
  font-size: 1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 45px;
  padding: 13px 20px;
  z-index: 1;
}

.catalog-product__info-link:hover::before {
  transform: scaleX(1.4) translateY(-50%);
}

.catalog-product__info-link::before {
  content: '';
  position: absolute;
  background: url("../images/arrow-right.svg") no-repeat center;
  top: 50%;
  right: 15px;
  filter: brightness(100%);
  width: 24px;
  height: 4px;
  transform: translateY(-50%);
  transition: transform 0.15s;
  transform-origin: left;
}

.company {
  position: relative;
  padding: 250px 0 150px;
}

@media screen and (max-width: 769px) {
  .company {
    padding: 100px 0 50px;
  }
}

.company::before {
  content: '';
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  background-color: #313E50;
  transform: skewY(-4deg);
  width: 120%;
  height: 100%;
}

.company::after {
  content: url("../images/bg-figure-2.svg");
  position: absolute;
  top: -25px;
  left: 0;
  transform: skewY(2.3deg) rotate(180deg);
  z-index: 0;
}

@media screen and (max-width: 769px) {
  .company::after {
    content: '';
  }
}

.product-list {
  margin-top: 40px;
}

.product-list__item {
  color: #fff;
  margin-top: 10px;
  font-size: 0.875rem;
  position: relative;
}

.product-list__item::before {
  content: '';
  position: absolute;
  background-color: #FB0201;
  left: -20px;
  top: 4px;
  width: 8px;
  height: 8px;
}

.company-product-wrapper {
  margin-top: 50px;
  padding: 0;
}

.company-product {
  width: 170px;
  height: 170px;
  cursor: pointer;
  border: 12px solid #000;
  padding: 45px 10px 0;
  transition: background-color 0.2s;
  margin-right: 25px;
}

.company-product:last-child {
  margin-right: 0;
}

.company-product:hover {
  background-color: #fff;
}

.company-product:hover .company-product__text {
  opacity: 1;
}

@media screen and (max-width: 769px) {
  .company-product {
    margin-right: 0;
  }
  .company-product .company-product__text {
    opacity: 1;
  }
}

.company-product--light {
  background: #FB0201 url("../images/company-pr-1.svg") no-repeat center;
}

.company-product--light:hover {
  background: #fff url("../images/hover-1.svg") no-repeat center 13px;
}

@media screen and (max-width: 769px) {
  .company-product--light {
    background: #fff url("../images/hover-1.svg") no-repeat center 13px;
  }
}

.company-product--medium {
  background: #FB0201 url("../images/company-pr-2.svg") no-repeat center;
  margin-top: 40px;
}

.company-product--medium:hover {
  background: #fff url("../images/hover-2.svg") no-repeat center 13px;
}

@media screen and (max-width: 769px) {
  .company-product--medium {
    background: #fff url("../images/hover-2.svg") no-repeat center 13px;
  }
}

.company-product--hard {
  background: #FB0201 url("../images/company-pr-3.svg") no-repeat center;
  margin-top: 80px;
}

.company-product--hard:hover {
  background: #fff url("../images/hover-3.svg") no-repeat center 13px;
}

@media screen and (max-width: 769px) {
  .company-product--hard {
    margin-top: 40px;
    background: #fff url("../images/hover-3.svg") no-repeat center 13px;
  }
}

.company-product__text {
  opacity: 0;
  text-align: center;
  font-size: 0.875rem;
  line-height: 1.7;
  transition: opacity 0.2s;
}

.product-list-title {
  margin-top: 120px;
}

@media screen and (max-width: 769px) {
  .product-list-title {
    margin-top: 0;
  }
}

.footer {
  background: #1F2733;
  color: #f2f2f2;
  padding: 20px 0;
}

.footer__logo {
  max-width: 130px;
  margin-right: 20px;
  vertical-align: middle;
}

.contacts {
  position: relative;
}

.contact-form {
  background-color: #1F2733;
  width: 100%;
  max-width: 570px;
  padding: 60px 80px;
  z-index: 4;
  position: absolute;
  bottom: 100px;
}

@media screen and (max-width: 769px) {
  .contact-form {
    left: 0;
    bottom: 500px;
  }
}

.contact-form__item {
  margin-top: 30px;
  position: relative;
}

.contact-form__label {
  color: #C2F9FF;
  font-size: 0.875rem;
  position: absolute;
  left: 24px;
  background: #1F2733;
  padding: 7px;
  top: 0;
  transform: translateY(-50%);
}

.contact-form__input {
  width: 100%;
  border: 1px solid #C2F9FF;
  background-color: #1F2733;
  height: 40px;
  padding: 5px 10px;
  color: #fff;
}

.address {
  position: absolute;
  background: #fff;
  top: 50%;
  right: 20%;
  transform: translateY(-50%);
  padding: 20px 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.adress__item {
  margin-top: 20px;
  font-size: 14px;
  text-align: center;
  line-height: 1.7;
}

.adress__item:first-child {
  margin-top: 0;
}

.adress__item--mail {
  font-weight: bold;
}

.hero {
  min-height: 90vh;
  padding: 280px 0 0;
  background: url("../images/hero-bg.jpg") no-repeat;
  z-index: 1;
  background-size: cover;
  position: relative;
}

@media screen and (max-width: 769px) {
  .hero {
    padding: 170px 0 0;
  }
}

.hero .container {
  position: relative;
  z-index: 1;
}

.hero::after {
  content: url("../images/bg-figure.svg");
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

@media screen and (max-width: 769px) {
  .hero::after {
    content: '';
  }
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(100deg, #000, transparent);
}

.nav {
  position: fixed;
  top: 50%;
  padding: 0 20px 35px;
  transform: translateY(-50%);
  left: 0;
  z-index: 7;
  background-color: #060814;
  transform-origin: top;
  transition: transform 0.5s;
}

.nav.is-visible {
  transform: scaleY(1);
}

@media screen and (max-width: 769px) {
  .nav {
    height: 100vh;
    width: 100%;
    top: 0;
    transform: scaleY(0);
    padding: 80px 20px 35px;
  }
}

.burger {
  position: fixed;
  background: url("../images/burger.svg") no-repeat;
  background-size: cover;
  z-index: 7;
  top: 30px;
  right: 10px;
  border: 0;
  width: 45px;
  height: 30px;
  display: none;
}

@media screen and (max-width: 769px) {
  .burger {
    display: block;
  }
}

.nav__close {
  display: none;
  position: absolute;
  top: 40px;
  right: 40px;
  width: 30px;
  height: 30px;
  border: 0;
  background: #060814 url("../images/close.svg") no-repeat center;
}

@media screen and (max-width: 769px) {
  .nav__close {
    display: block;
  }
}

.nav__link {
  display: block;
  margin-top: 25px;
  color: #69747C;
}

@media screen and (max-width: 769px) {
  .nav__link {
    font-size: 33px;
    margin-top: 40px;
  }
}

.nav__link.is-active {
  font-weight: bold;
  color: #fff;
}

.nav__link--logo {
  background: url("../images/logo-red.svg") no-repeat;
  background-size: 100%;
  width: 95px;
  height: 22px;
}

@media screen and (max-width: 769px) {
  .nav__link--logo {
    width: 180px;
    height: 50px;
  }
}

.warranty {
  background: url("../images/warranty.svg") no-repeat 25% 20px;
  padding: 40px 0 70px;
}

@media screen and (max-width: 769px) {
  .warranty {
    background: url("../images/warranty.svg") no-repeat 8% 40px;
    background-size: 30%;
    padding: 10px 0 50px;
  }
}

.warranty__text {
  max-width: 800px;
  font-weight: bold;
  font-size: 2.25rem;
  line-height: 1.5;
  color: #000;
  margin-left: 40%;
}

@media screen and (max-width: 769px) {
  .warranty__text {
    font-size: 1.8rem;
  }
}

.lines {
  transform: rotate(-45deg);
  display: flex;
  position: fixed;
  z-index: 3;
  flex-direction: column;
  justify-content: space-between;
}

.lines__item {
  border-right: 1px solid #f2f2f2;
  width: 33%;
  height: 100%;
}

.slider {
  max-height: 300px;
  max-width: 600px;
  margin-top: 40px;
  position: relative;
  object-fit: cover;
}

.slider::before {
  content: '';
  position: absolute;
  background: url("../images/about-1.png") no-repeat;
  opacity: 0.4;
  background-size: cover;
  height: 170%;
  width: 700px;
  top: 65%;
  z-index: -1;
  right: 0;
  transform: translate(50%, -50%);
}

.slider__item {
  display: block;
  height: 300px;
}

.slider--about::before {
  content: '';
  left: 0;
  transform: translate(-50%, -50%);
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
  font-size: 16px;
  font-weight: 300;
}

@media screen and (max-width: 769px) {
  html, body {
    font-size: 12px;
  }
}

@media screen and (max-width: 374px) {
  html, body {
    font-size: 10px;
  }
}

body {
  font-family: 'Google Sans', sans-serif;
  font-weight: normal;
  animation: fadein 1s;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

strong {
  font-weight: bold;
}

.container {
  max-width: 1700px;
  padding: 0 20px;
  margin: 0 auto;
}

@media screen and (max-width: 1720px) {
  .container {
    max-width: 1200px;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
